.todowrapper{
    display: flex;
    align-items: center;
    background-color: rgba(8,26,82);
    color: white;
    width: 90%;
    min-height: 50px;
    justify-content: center;
    margin: 3px;
    border-radius: 10px;
    justify-content: space-between;
    box-shadow: 2px 2px rgba(0,0,0,0.2);
    input{
        border: none;
        outline: none;
        
    }
}